.page-leagues {
  margin-top: 15px;
}

.page-leagues-new {
  float: right;
}

.page-leagues-new:before {
  font-family: FontAwesome;
  content: '\f067';
  margin-right: 8px;
}

.page-leagues-list {
  clear: both;
  display: table;
  padding-top: 10px;
  width: 100%;
}

.page-leagues-list-item {
  display: block;
  background-color: #e5e5e5;
  padding: 6px 11px 3px 11px;
  margin-top: 2px;
  font-weight: 600;
  color: @primaryTextColor;
}

.page-leagues-list-item:hover {
  background-color: @primaryTextColor;
  color: #e5e5e5;
}

.page-leagues-column {
  text-transform: uppercase;
  color: #aaa;
  font-size: .8em;
  border-bottom: 1px #999 solid;
  font-weight: 600;
  padding-left: 5px;
}